<template> <!-- things under the Template header Where All the HTML is Stored -->
    <div id="myHeader">
        <ul>
            <li v-for="items in myArray" @click="changeIndex(items.comTitle,items.imgIndex)">
                {{items.comTitle}}
            </li>
        </ul>
    </div>
</template>

<script> //everything is one big component
export default { 
  name: 'Header',
  data () {
    return {
        myArray: [
            {comTitle: "Get",imgIndex:0},
            {comTitle: "Put",imgIndex:1},
            {comTitle: "Post",imgIndex:2},
            {comTitle: "Delete",imgIndex:3}
        ],
        
    }
  },
  methods: {
      changeIndex: function(event,index) {
          this.$emit('emittedEvent',event);
          this.$emit('emittedImgEvent',index);
      }
  },
//   created () {
//       this.$http.get('https://02sqmhhti2.execute-api.us-west-2.amazonaws.com/dev/get').then(function(data) {
//           this.stuff = data;
//       });
//   }
}
</script>

<style scoped>
  #myHeader {
    height: 10%;
    display: flex;
  }
  ul {
    display: flex;
    width: 100%;
    margin: auto;
    padding: 0;
    justify-content: space-evenly;
    font-size: 2rem;
  }
  li {
    list-style-type: none;
    color: white;
    border: 3px solid white;
    width: 23%;
    text-align: center;
    border-radius: 5rem;
  }
  li:hover {
      cursor: pointer;
  }
</style>



