<template> <!-- things under the Template header Where All the HTML is Stored -->
    <div class="forms">
        <h2 class="formTitle">Delete a Student:</h2>
        <input class="formInput" type="text" v-model="post.value" placeholder="Currently Not Working" />
        <button class="formButton" @click="deleteContent()">Submit</button>
        <myMessage v-if="posted" :methodUsed="'Deleting'"></myMessage>
    </div>
</template>

<script> //everything is one big component
export default { 
  name: 'elements', 
  props: {

  },
  data () {
    return {
        posted: false,
        post: {
            value: '',
        }
    }
  },
  methods: {
      deleteContent: function() {
          this.$http.delete('https://02sqmhhti2.execute-api.us-west-2.amazonaws.com/dev/delete',this.post);
          this.posted = true;
      }
  }
}
</script>

<style>
</style>


