<template> <!-- things under the Template header Where All the HTML is Stored -->
    <div id="imageContainer">
        <img :src="images[count]" alt="">
    </div>
</template>

<script> //everything is one big component
export default { 
  props: {
      count: {
      }
  },
  name: 'Images', 
  data () {
    
    return {
        images: [
            './src/assets/donut.gif',
            './src/assets/spin.gif',
            './src/assets/skele.gif',
            './src/assets/prey.gif'
        ]
    }
  },
  methods: {

  }
}
</script>

<style scoped>
  #imageContainer {
    display: inline-flex;
    width: 50%;
    height: 100%;
    justify-content: center;
  }
  img {
      display: inline-block;
      max-width: 50%;
      max-height: 50%;
      margin: auto;
  }
</style>



