import Vue from 'vue'
import App from './App.vue'
import VueResource from 'vue-resource'
import Message from './components/message.vue'
//Bridge of the Vue script to the HTML script

Vue.use(VueResource);

Vue.component('myMessage', Message);

new Vue({
  el: '#root', //where everything will render to in the html
  render: h => h(App) //renders the App.vue to HTML
})


