<template> <!-- things under the Template header Where All the HTML is Stored -->
    <div class="forms">
        <h2 class="formTitle">Update a Student:</h2>
        <input class="formInput" type="text" v-model="post.oldValue" placeholder="Old Student Name" />
        <input class="formInput" type="text" v-model="post.newValue" placeholder="New Student Name" />
        <button class="formButton" @click="updateContent()">Submit</button>
        <myMessage v-if="posted" :methodUsed="'Updating'"></myMessage>
    </div>
</template>

<script> //everything is one big component
export default { 
  name: 'elements', 
  props: {

  },
  data () {
    return {
        posted: false,
        post: {
            newValue: '',
            oldValue: ''
        }
    }
  },
  methods: {
      updateContent: function() {
          this.$http.put('https://02sqmhhti2.execute-api.us-west-2.amazonaws.com/dev/put',this.post);
          this.posted = true;
      }
  }
}
</script>

<style>
</style>


