<template> <!-- things under the Template header Where All the HTML is Stored -->
    <div class="forms">
        <h2 class="formTitle">Things Posted in CD realm:</h2>
        <div id="displayContainer">
            <div class="container" v-for="items in GetItem">
                <h3>grade: {{items.grade_level}}</h3>
                <h2>{{items.name}}</h2>
            </div>
        </div>
    </div>
</template>

<script> //everything is one big component
export default { 
  name: 'elements', 
  props: {

  },
  data () {
    return {
        GetItem: '',
    }
  },
  methods: {

  },
  created () {
      this.$http.get('https://02sqmhhti2.execute-api.us-west-2.amazonaws.com/dev/get').then(function(data){
          let tempData = JSON.parse(data.data.body); //parses it to Objects as its just string in an object
          this.GetItem = tempData.message.rows;
      });
  }
}
</script>

<style scoped>
  .forms {
      display: inline-flex;
      flex-direction: column;
      width: 50%;
      height: 100%;
      color: white;
  }

  .formTitle {
      text-align: center;
      font-size: 1.65rem;
      margin: 0;
      padding: 1rem;
  }

  #displayContainer {
      height: 60rem;
      overflow: scroll;
      overflow-x: hidden;
  }

  .container {
    border: 4px solid whitesmoke;
    width: 30rem;
    height: 15rem;
    margin: 1rem auto;
    overflow: hidden;
    border-radius: 1.5rem;
  }

  .container > h2 {
    font-size: 3rem;
    text-align: center;
  }

  .container > h3 {
      margin: 0;
      padding: 0.75rem;
      text-align: center;
      background: linear-gradient(45deg, rgb(240, 199, 255) 0%, rgba(250,213,255,1) 40%, rgba(93,228,255,1) 100%);
  }

</style>


