<template> <!-- things under the Template header Where All the HTML is Stored -->
    <div id="root">
        <myHeader v-on:emittedEvent="changeComponent($event)" v-on:emittedImgEvent="changeImage($event)"> </myHeader>
        <div id="main">
            <myImages :count="imageIndex"> </myImages>
            <keep-alive>
                <component :is="currentComponent"> </component>
            </keep-alive>
        </div>
    </div>
</template>

<script> //everything is one big component
import Header from './components/Header.vue';
import Images from './components/Images.vue';
import GetForm from './components/GetForm.vue';
import PostForm from './components/PostForm.vue';
import DeleteForm from './components/DeleteForm.vue';
import PutForm from './components/PutForm.vue';
export default { 
  components: { 
      'myHeader': Header,
      'myImages': Images,
      'Get': GetForm,
      'Put': PutForm,
      'Post': PostForm,
      'Delete': DeleteForm,
  }, 
  name: 'app', 
  data () {
    return {
      imageIndex: 0,
      currentComponent: 'Get'
    }
  },
  methods: {
      changeComponent: function(event) {
          console.log(`Changed to ${event}`)
          this.currentComponent = event;
      },
      changeImage: function(event) {
          this.imageIndex = event;
      }
  }
}
</script>

<style>
  * {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  }
  #root, body, html {
    width: 100%;
    height: 100%;
    margin: 0;
    background: rgb(222, 152, 255);
  }
  #main {
      display: flex;
      height: 90%;
  }
  .forms {
      display: inline-flex;
      flex-direction: column;
      width: 50%;
      height: 100%;
      color: white;
  }

  .formTitle {
      text-align: center;
      font-size: 1.65rem;
  }

  .formInput {
      background: transparent;
      border: 5px solid white;
      width: 50%;
      height: 2rem;
      padding: 0.5rem;
      font-size: 1.5rem;
      margin: 1rem auto;
      color: white;
      border-radius: 5rem;
  }

  .formInput::-webkit-inner-spin-button { 
      display: none;
   }    

  .formButton {
      width: 50%;
      background: transparent;
      height: auto;
      padding: 1rem;
      text-align: center;
      border: 3px solid white;
      margin: 1rem auto;
      font-size: 2rem;
      color: white;
      border-radius: 0.5rem;
  }
  .formButton:hover {
      cursor: pointer;
  }
</style>



