<template> <!-- things under the Template header Where All the HTML is Stored -->
    <div class="forms">
        <h2 class="formTitle">Post a Student:</h2>
        <input class="formInput" type="text" v-model="post.name" placeholder="Student Name" />
        <input class="formInput" type="number" v-model="post.grade_level" placeholder="Student Grade" />
        <button class="formButton" @click="postContent()">Submit</button>
        <myMessage v-if="posted" :methodUsed="'Post'"></myMessage>
    </div>
</template>

<script> //everything is one big component
export default { 
  name: 'elements', 
  props: {
  },
  data () {
    return {
        posted: false,
        post: {
            name: '',
            grade_level: ''
        }
    }
  },
  methods: {
      postContent: function() {
          this.$http.post('https://02sqmhhti2.execute-api.us-west-2.amazonaws.com/dev/post', this.post);
          this.posted = true;
      }
  }
}
</script>

<style>
</style>


