    </div>
</template>

<script> //everything is one big component
export default { 
  name: 'message', 
  props: {
      methodUsed: {
          type: String,
          required: true
      }
  },
  data () {
    return {

    }
  },
  mounted () {
      setTimeout(function(){
          location.reload();
      },2000)
  }
}
</script>

<style scoped>
#messageBox {
    width: 50%;
    margin: 0 auto;
    padding: 1rem;
    border: 5px solid white;
    border-radius: 2rem;
    font-size: 1.079rem;
}
</style>


